import React from 'react'
import img1 from '../../common/img/carousel/1.png'
import img2 from "../../common/img/carousel/2.png"
import img3 from "../../common/img/carousel/3.png"
import img4 from "../../common/img/carousel/4.png"
import img5 from "../../common/img/carousel/5.png"
import './index.css'
export default function Carousel(props) {
    React.useEffect(()=>{
        // const pointEle = document.querySelector('.content .carousel')
        const allImg = document.querySelectorAll('.carousel-img-item')
        const allPoint = document.querySelectorAll('.carousel-point-item')
        allPoint[0].style.backgroundColor = 'orange'
        let i = 0;
        let carouselFun=''
        if(document.body.clientWidth>500){
             carouselFun=() => {
                allImg[i].style.width = '0vw'
                i += 1
                if (i === props.img.length) {
                    for (let j = 0; j < props.img.length; j++) {
                        allImg[j].style.width = '35vw'
                    }
                    allPoint[props.img.length-1].style.backgroundColor = ''
                    allPoint[0].style.backgroundColor = 'orange'
                    i = 0;
                } else {
                    allPoint[i].style.backgroundColor = 'orange'
                    allPoint[i - 1].style.backgroundColor = ''
                }
                let timer = setTimeout(carouselFun, 3000);
                // let paddingRight = pointEle.style.paddingRight
                // if (paddingRight === '') {
                //     pointEle.style.paddingRight = '0vw'
                // } else {
                //     let rightNumber = Number(paddingRight.split('v')[0])
                //     pointEle.style.paddingRight = rightNumber + 26 + 'vw'
                // }
            }
        }else{
             carouselFun=() => {
                allImg[i].style.width = 0
                i += 1
                if (i === props.img.length) {
                    for (let j = 0; j < props.img.length; j++) {
                        allImg[j].style.width = '50vw'
                    }
                    allPoint[props.img.length-1].style.backgroundColor = ''
                    allPoint[0].style.backgroundColor = 'orange'
                    i = 0;
                } else {
                    allPoint[i].style.backgroundColor = 'orange'
                    allPoint[i - 1].style.backgroundColor = ''
                }
                let timer = setTimeout(carouselFun, 3000);
        }
    }
        let timer = setTimeout(carouselFun, 2000);
        return ()=>{
          clearInterval(timer)
      }
          },[])
    return (
        <div className="carousel">
            {/* 不要随便加div，会影响定位 */}
            {/* <div className='carousel-img'> */}
            {
                props.img.map((item,index)=>{
                    return (
                        <div className="carousel-img-item" key={index}> <img src={item} alt=""/></div>
                    )
                })
            }
        {/* </div> */}
        <div className="carousel-point">
        {
                props.img.map((item,index)=>{
                    return (
                        <span className="carousel-point-item" key={index}></span>
                    )
                })
            }
        </div>
    </div>

    )
}
